@use "global" as *;

.sheet-list {
  @include mq(md){
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: rem(40);
    grid-row-gap: rem(80);
  }
}

.sheet-list__item + .sheet-list__item {
  margin-top: rem(40);
  @include mq(md){
    margin-top: 0;
  }
}